import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { RatePageRootComponent } from "./rate-page-root.component";
const routes: Routes = [
  {
    path: "",
    component: RatePageRootComponent,
    children:[
      {
        path:'schedule-of-rates',
        loadChildren:'./schedule-of-rates/schedule-of-rates.module#ScheduleOfRatesModule'
      },
    
      {
        path:'',
        redirectTo:'schedule-of-rates',
        pathMatch:'full'
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RatePagesRootRoutingModule {}
