import { Component, OnInit } from "@angular/core";
import { Router, NavigationStart } from "@angular/router";

@Component({
  selector: "app-rate-page-root",
  templateUrl: "./rate-page-root.component.html",
  styleUrls: ["./rate-page-root.component.scss"]
})
export class RatePageRootComponent implements OnInit {
  public name: string;
  public url: string;

  constructor(private router: Router) {}

  ngOnInit() {
    this.display();

    this.router.events.subscribe(event => {
      // console.log(event);
      //可以用instanceof来判断事件的类型，然后去做你想要做的事情
      // console.log(event instanceof NavigationStart);
      // console.log('123');

      this.display();
    });
  }
  display() {
    this.url = window.location.href;
    if (this.url.indexOf("add-rate") != -1) {
      this.name = " > 设置费率表";
    } else {
      this.name = "";
    }
  }
}
